body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0px;
  background-color: #083346;
}
img {
  pointer-events: none; /* Disable pointer events on images */
}
.wrapper {
    display: grid;
    grid-gap: 0px;
    grid-template-rows: 1fr 5fr 5fr;
		grid-template-columns:1fr 1fr 1fr;
		
		color: #ffffff;
    height:100vh;
   
	}

	.box {
    /*display:flex;
    justify-content: center;
    align-items: center;
    padding:0px;*/
    border:none;
	}
  .box.active {
    opacity: .6;
    
   
	}
  
  .box img{
    max-width: 90%;
    max-height: 90%;
  }

  .header {
		grid-column: 1/4;
    grid-row:1;
    background:hsl(193, 73%, 20%);
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
     background-size:contain;
    background-repeat: no-repeat;
    background-position: center center;
    color:#ffffff;
    font-size:30px;
    display: flex; /* Use flexbox for vertical alignment */
    justify-content: center; /* Center horizontally */
    align-items: center;
    text-transform: capitalize;
  }

	.a {
    display:block;
		grid-row: 2;
    background:#0196C1;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    background-image: url( "./images/up-arrow.png");
     background-size:contain;
    background-repeat: no-repeat;
    background-position: center center;
	}
	.b {

		grid-row: 3;
    background:#48B5D6;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    background-image: url( "./images/down-arrow.png");
     background-size:contain;
    background-repeat: no-repeat;
    background-position: center center;
	}
	.c {
		grid-column:2;
    grid-row:2/4;
    background:#046C95;
    box-shadow: inset 4px 0px 4px rgba(0, 0, 0, 0.25);
    background-image: url( "./images/left-arrow.png");
    background-size:contain;
    background-repeat: no-repeat;
    background-position: center center;
	}
	.d {
    grid-column:3;
		grid-row: 2/4;
    background:#083346;
    box-shadow: inset 4px 0px 4px rgba(0, 0, 0, 0.25);
    background-image: url( "./images/right-arrow.png");
     background-size:contain;
    background-repeat: no-repeat;
    background-position: center center;
	}
  
  .single-button-container {
    grid-column: 1 / -1; /* Span the full width of the grid */
    display: flex;
    justify-content: center; /* Center the button */
    align-items: center; /* Center the button vertically within the grid cell */
    width: 100%; /* Full width of the container */
  }
  
  .single-button {
    
    width: 100%; /* Full width of the button */
    max-width: 300px; /* Optional: Max width for the button */
    height: 200px; /* Height of the button */
    background-color: rgb(166, 41, 41);
    color: rgb(255, 248, 248);
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 50px; /* Adjust to keep rounded appearance */
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px #3b3b3b; /* Adds a shadow effect */
    margin: 10px; /* Remove default margin */
   
    transition: transform 0.1s, box-shadow 0.1s; /* Smooth transition for pressed effect */
    -webkit-tap-highlight-color: transparent;
  }
  
  .single-button:active {
    background-color: rgb(112, 7, 7);
    box-shadow: 0 2px #666;
    transform: translateY(2px); /* Adds a pressed effect */
  }
  .roomstatus{
    padding-right:10px;
    padding-left:10px;
  }